@import "../../assets/variables";

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 60px;
  padding: 0.5em 4em;
  font-size: 20px;

  .logo h1 {
    padding: 0;
    margin: 0;

    .nails,
    .by {
      font-family: $broadway;
      font-weight: normal;
      color: $link-color;
      text-shadow: 0 3px 6px rgba(0, 0, 0, 0.32);
    }
    .nails {
      font-size: 48px;
    }
    .by {
      font-size: 36px;
      margin-left: 0.5em;
    }
  }

  .menu-items {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;

    .menu-item {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 1em;

      &:last-child a {
        opacity: 1;
      }

      a {
        color: $dark-color;
        transition: 0.3s;
        opacity: 0.8;

        &:hover {
          opacity: 1;
        }

        .button {
          color: $light-color;
        }
      }
    }
  }
}
