@import "../../assets/variables";
.pick-procedure-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: calc(100vh - 80px);

  background-image: url("../../assets/images/allison-christine-n4MHxHD1dKI-unsplash-edited.jpg");
  background-size: cover;
  background-position: center;

  background-size: cover;
  background-position: center;

  .title-container {
    display: block;
    width: 790px;
    margin: 0 auto;
    margin-top: 85px;
    margin-bottom: 40px;

    .action-prompt {
      font-size: 18px;
      color: $lighter-dark-color;
      margin: 0;
      text-align: center;
    }

    h2 {
      color: $dark-color;
      font-size: 32px;
      font-weight: 300;
      text-align: center;
      margin: 0;
    }
  }

  .procedures {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-bottom: 85px;

    .procedure {
      width: 250px;
      .procedure-title {
        color: $link-color;
        font-size: 32px;
        font-weight: bold;
        text-shadow: $shadow;
        margin: 0;
      }

      .procedure-details {
        padding-bottom: 1em;
      }
    }
  }
}
