@import "../../assets/variables";

.pick-day {
  margin-top: 4em;
  .hint {
    font-size: 24px;
    color: $dark-color;
  }

  .days {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    .day-link {
      color: $link-color;
      cursor: pointer;
      transition: color 0.4s;

      .day-of-week {
        font-size: 28px;
        margin-bottom: -9px;
      }

      .date {
        font-size: 18px;
      }

      &:hover {
        color: $link-color-hover;
      }

      &.active {
        color: $link-color-active;
      }
    }
  }
}
