@import "../../assets/variables";

.contact-page {
  h2 {
    color: $dark-color;
  }

  form {
    display: block;
    width: 1000px;
    margin: 0 auto;

    label {
      display: block;
    }

    input,
    textarea {
      display: block;
      padding: 1em;
      background-color: white;
      border: 1px solid #ccc;
      width: 100%;
      resize: none;
    }

    @media screen and (max-width: 1100px) {
      width: 100%;
    }
  }
}
