@import "../../assets/variables";

@keyframes zoom-background-in-out {
  0% {
    left: 0;
    width: 100vw;
  }

  50% {
    left: -10vw;
    width: 120vw;
  }

  100% {
    left: 0;
    width: 100vw;
  }
}

.homepage {
  height: 100%;

  .background-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    z-index: -1;

    background-image: url("../../assets/images/allison-christine-n4MHxHD1dKI-unsplash-edited.jpg");
    background-size: cover;
    background-position: center;

    animation-name: zoom-background-in-out;
    animation-timing-function: ease-in-out;
    animation-duration: 12s;
    animation-iteration-count: infinite;
  }

  .cta-container {
    h2 {
      color: $light-color;
      font-weight: bold;
      font-size: 36px;
      text-shadow: $shadow;
      display: block;
      width: 720px;
    }
  }
}
