@import "../../assets/variables";

.button {
  display: inline-block;
  padding: 0.3em 1.2em;

  background-color: $link-color;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.11);

  font-size: 20px;
  color: $light-color;
  font-family: $poppins;

  cursor: pointer;
  transition: background-color 0.3s;

  &:hover {
    background-color: $link-color-hover;
  }
}
